import React from "react";

import Layout from "../components/layout";

import Button from "react-bootstrap/Button";
import Collapse from "react-bootstrap/Collapse";
import Badge from "react-bootstrap/Badge";
import Container from "react-bootstrap/Container";

import dictionary_ru_project_expert from "../laboratory/dictionary_ru_project_expert";


let report_data = [{
    ru_title: "Сдельная зарплата",
    value: 100
}];

let report_fields = [
    "Суммарные прямые издержки",
    "Сдельная зарплата",
]

export default function Budgeting() {
    return <Layout>
        {report_fields.map(field => <div key={field}>{calculatedValue(field)}</div>)}

        {dictionary_ru_project_expert.map(item => <Indicator key={item.ru_title} indicator={item} />)}
    </Layout>
}

function look_down(ru_title) {
    let items = dictionary_ru_project_expert.filter(item => item.ru_title === ru_title);
    let dependent = !!items && items[0]?.dependent;
    let calculatedValue = null;
    dependent.map(item => {
       console.log(calculatedValue(item))
    })
    return dependent.length
}

function calculatedValue(props) {
    let value = report_data.filter(item => item.ru_title === props.ru_title);
    value = value.length > 0 ? value[0].value : '_'; //look_down(props.ru_title)
    return value
}

function Indicator(props) {
    const [open, setOpen] = React.useState(false);

    if (props?.indicator?.dependent && props.indicator.dependent.length > 0) {
        return <Container>
            <Button
                onClick={() => setOpen(!open)}
                aria-controls="example-collapse-text"
                aria-expanded={open}
                variant="outline-secondary"
            >
                {props.indicator.ru_title} <Badge variant="secondary">{props.indicator.dependent.length}</Badge>
            </Button>
            <Collapse in={open} className="ml-3">
                <div id="example-collapse-text">
                    {props.indicator.dependent.map(dependentItem => {
                        let show_indicator = dictionary_ru_project_expert.filter(item => item.ru_title === dependentItem)[0];
                        // console.log(dependentItem);
                        // console.log(show_indicator);
                        // console.log('_________________');
                        return <Indicator key={props.indicator.ru_title + '___' + dependentItem}
                            indicator={show_indicator}
                        />
                        // <div key={props.indicator.ru_title + '___' + dependentItem}>{dependentItem}</div>
                    }
                    )}
                </div>
            </Collapse>
        </Container>
    }

    return <div>{props.indicator.ru_title}</div>
} 